<template>
    <nav class="navigation">
        <div class="container">
            <div class="nav__logo">
            <img :src="Logo" />
        </div>
        <div class="nav__title">
            <router-link class="" to="/"><h1>Sheet</h1></router-link>
        </div>
    </div>
        
    </nav>
</template>

<script>
import Logo from '../assets/Logo.png' 

    export default {
        name: 'Navigation',
        data: function () {
            return {
                Logo: Logo
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .navigation
        background white
        width 100%
        padding-top 1em
        padding-bottom 1em
        box-shadow 0px 0px 30px 0px rgba(0,0,0,0.07)     
        
    .navigation > .container
        display flex
        margin 0 auto

    .nav__logo
        display flex
        align-items center

    .nav__title
        margin-left 2em
        display flex
        align-items center
    
    .nav__title > A >h1
        margin-bottom 0

    .nav__logo > img {
        width 50px
    }
</style>


