                        <h2>Nová faktúra</h2>
                  </router-link>
              </div>
          </div>
      </div>
      
  </div>
</template>

<script>
import InvoiceLogo from './assets/invoice.png'

export default {
  name: 'Home',
  data: function () {
        return {
            InvoiceLogo: InvoiceLogo
        }
    }
}

</script>

<style lang="stylus" scoped>

    .col
        width 33.3%
        margin 1em
        box-shadow 0px 0px 30px 0px rgba(0,0,0,0.07)
        transition box-shadow .2s

    .col:hover
        box-shadow 0px 0px 30px 0px rgba(0,0,0,0.2)

    .row > .col:first-child
        margin-left 0

    .row > .col:last-child
        margin-right 0


    .grid__action
        background white
        width 100%
        display flex
        padding 2em
        justify-content center 
        align-items center
    
    .grid__action > img
        width 150px
    
    .grid__action > h2 
        margin-left .5em
        margin-bottom 0

</style>


